<template>
  <div class="table-striped table-responsive">
    <table class="table mb-0 text-center">
      <thead>
        <tr>
          <th style="width: 5%" scope="col">No</th>
          <th style="width: 10%" scope="col">Kode MK</th>
          <th style="width: 30%" scope="col">Nama MK</th>
          <th scope="col">Nama Kelas</th>
          <th scope="col">Hadir</th>
          <th scope="col">Alfa</th>
          <th scope="col">Izin</th>
          <th scope="col">Sakit</th>
          <th scope="col">File Rekap</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(kehadiranmhs7, index) in getDataHadir7"
          :key="kehadiranmhs7.course_code"
        >
          <td>{{ number(index) }}</td>
          <td>{{ kehadiranmhs7.course_code }}</td>
          <td class="text-left">{{ kehadiranmhs7.name_ina }}</td>
          <td>{{ kehadiranmhs7.class_name }}</td>
          <td>{{ kehadiranmhs7.present }}</td>
          <td>{{ kehadiranmhs7.absent }}</td>
          <td>{{ kehadiranmhs7.permission }}</td>
          <td>{{ kehadiranmhs7.sick }}</td>
          <td class="table-action text-center">
            <button class="btn btn-success">
              <i class="fas fa-download"></i>
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Semester7",
  data() {
    return {
      kehadiranmhs7: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataHadir7: "kehadiran/mhshadir7",
    }),
  },
  methods: {
    ...mapActions({
      hadiran: "kehadiran/kehadiran",
    }),
    number: function (index) {
      return index + 1;
    },
    getKehadiranAll() {
      this.hadiran();
    },
  },
  mounted() {
    this.getKehadiranAll();
  },
};
</script>
